export default {
  name: 'OnlineStatusSelect',
  props: {
    checked: {
      type: Boolean,
      default: false
    },
    icon: {
      type: String,
      required: true
    },
    type: {
      type: String,
      required: true
    },
    label: {
      type: String,
      required: true
    },
    subline: {
      type: String,
      default: null
    }
  },
  computed: {
    id() {
      return `user-status-online-status-${this.type}`;
    }
  },
  methods: {
    onChange() {
      this.$emit('select', this.type);
    }
  }
};